import { Component, OnInit,ViewContainerRef,ViewChild } from '@angular/core';
import { AppGlobal } from '../../app-global';
import {Router,ActivatedRoute}  from '@angular/router';
import { MyServiceService } from '../../my-service.service';
import { LocalStorageService } from 'angular-2-local-storage';

@Component({
  selector: 'app-setting',
  templateUrl: './setting.component.html',
  styleUrls: ['./setting.component.css']
})
export class SettingComponent implements OnInit {
  appGlobal = AppGlobal.getInstance();
  settingList:any=[];
  groupIndex:number=0;
  categoryIndex:number=0;
  itemIndex:number=0;
  group:Object={};
  category:Object={};
  item:Object={};
  settingJson={};
  cashDrawers:any=[];
  constructor(private myService:MyServiceService,private router:Router,public aRoute:ActivatedRoute,private storeService: LocalStorageService) { 
  	 this.settingList=[{"group":"","categories":[{"items":[{"item":"","values":[{"name":"","type":"","value":[]}]}]}]}];
     this.item={"item":"","values":[{"name":"","type":"","value":[]}]};
     
  }
  ngOnInit() {
       this.myService.service("/settings/merchant/id","get").subscribe(
               data=> {
                   if(!!data){
                     console.log(data);
                     this.settingList=data;
                   
                     this.settingJson["020"]=[];
                     this.settingJson["020"][4]=[{"name":"test","value":false},{"name":"yyyy","value":false}];
                     

                     this.selectItem(this.settingList[0]["categories"][0]["items"][0]);
                     
                     this.cashDrawers=this.settingList[1]["categories"][1]["items"];
                     this.settingJson["12"]=[];
                                   

                    
                  

            }
                 }
                
            );
   
  }

  selectItem(item){
    this.item=JSON.parse(JSON.stringify(item));
   let cashDrawers=[];
   for(var i=0;i<this.cashDrawers.length;i++){
       let cashDrawer={};
           cashDrawer["name"] =this.cashDrawers[i]["item"];
           cashDrawer["value"] =false;
           cashDrawers.push(cashDrawer);
   }
   let itemKey=this.groupIndex+''+this.categoryIndex;
   
   if(this.settingJson[itemKey]){
            
             let obj3 = {};
             let k=1;
             for(var i=0;i<this.item["values"][k].value.length;i++){
                 if(this.item["values"][k].value[i]["value"]==true){
                    obj3[this.item["values"][k].value[i]["name"]]=true;
                 }
                
            }
            for(var i=0;i<cashDrawers.length;i++){
                   if(obj3[cashDrawers[i]["name"]]){
                       cashDrawers[i]["value"]=true;
                   }else{
                     cashDrawers[i]["value"]=false;
                   }
            }
           this.item["values"][k].value= cashDrawers;

   }


   let key=this.groupIndex+''+this.categoryIndex+''+this.itemIndex;
   if(this.settingJson[key] && !!this.settingJson[key][4]){
             let obj3 = {};
             let k=4;
             for(var i=0;i<this.item["values"][k].value.length;i++){
                 if(this.item["values"][k].value[i]["value"]==true){
                    obj3[this.item["values"][k].value[i]["name"]]=true;
                 }
                
            }
            for(var i=0;i<this.settingJson[key][k].length;i++){
                  console.log(obj3)
                   if(obj3[this.settingJson[key][k][i]["name"]]){
                       this.settingJson[key][k][i]["value"]=true;
                   }else{
                     this.settingJson[key][k][i]["value"]=false;
                   }
            }
           this.item["values"][k].value= this.settingJson[key][k];
   }

 
  }

  settings(n){
    /* let sendData= !!n?n:(this.cfSelect==0?{"type":"!Device"}:{"type":"Device"});
     if(this.cfSelect==0){
          this.settingType={"type":"Global"};
     }else{
         this.settingType={"type":"Printer"};
         
     }
  		this.myService.service("/settings/merchant/id","get",{},sendData).subscribe(
               data=> {
                   if(!!data){
                     console.log(data);
                   	this.settingForm=[];
                   	this.settingList=data;
                    if(this.cfSelect==0){
                        this.settingForm=this.settingList[0];  
                    }else{
                       this.settingForm=this.deviceArray[0];


                    }
                   

                    
                  

            }
                 }
                
            );*/
  }
  changeList(data){
   /* if(typeof(data)=="string"){
          for(var i=0;i<this.settingList.length;i++){
          if(data==this.settingList[i]["_id"]){
            
            this.settingList.splice(i,1);
          }
      }
    }else{
          for(var i=0;i<this.settingList.length;i++){
    		  if(data["_id"]==this.settingList[i]["_id"]){
    		  	
    		  	this.settingList[i]=data;
    		  }
    	}
  }*/
  }
  delete(){
        let tempItem=JSON.parse(JSON.stringify(this.item));
        this.myService.service("/settings/"+this.item["_id"],"delete").subscribe(
                 data=> {
               if(!!data){

                  for(let i=0;i<this.settingList[this.groupIndex]["categories"][this.categoryIndex]["items"].length;i++){
                        if(tempItem["_id"]==this.settingList[this.groupIndex]["categories"][this.categoryIndex]["items"][i]["_id"]){
                          this.settingList[this.groupIndex]["categories"][this.categoryIndex]["items"].splice(i,1);
                          this.add();
                        }
                  }
               }
             }
                  
          );
            
  }
  save(){
         console.log(this.item)
      if(this.item["_id"]){
            this.myService.service("/settings/"+this.item["_id"],"put",this.item).subscribe(
                 data=> {
                 if(!!data){
              this.settingList[this.groupIndex]["categories"][this.categoryIndex]["items"][this.itemIndex].values=data.values;
              this.settingList[this.groupIndex]["categories"][this.categoryIndex]["items"][this.itemIndex].item=data.item;     
               }
             }
                  
          );
      }else{
              this.myService.service("/settings","post",this.item).subscribe(
                   data=> {
                       if(!!data){
                      this.settingList[this.groupIndex]["categories"][this.categoryIndex]["items"].push(data);
                      this.itemIndex=this.settingList[this.groupIndex]["categories"][this.categoryIndex]["items"].length-1;
                 }
               }
                    
            );
      }
  		
  }
 add(){
     this.itemIndex=-1;
     this.item["item"]="";
     this.item["group"]=this.settingList[this.groupIndex].group;
     this.item["category"]=this.settingList[this.groupIndex]["categories"][this.categoryIndex].category;
     delete this.item["_id"];
     console.log(this.item)
    for(let i=0;i<this.item["values"].length;i++){
          if(this.item["values"][i]['type']=="input"){
            this.item["values"][i].value="";
          }else{
            for(let j=0;i<this.item["values"][i].value.length;j++){
                  this.item["values"][i].value[i]['value']="";
          }
  
          }
            }
 }
 change(vv){
    for(var i=0;i<vv.value.length;i++){
           if(vv.value[i]["name"]==vv["key"]){
             vv.value[i]['value']=true;
           }else{
             vv.value[i]['value']=false;
             //vv.value[i].value=value;
           }
    }
 }
}



